.background {
    background: url(https://thumbs.gfycat.com/GiftedViciousCrab-mobile.mp4)
    repeat;
    height: 100vh;
    width: 100%;
    opacity: 0.7;
    position: absolute;
}
.App {
    position: absolute;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    z-index: 1;
    }
  
  .center {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  input[type="text"] {
    height: 30px;
    width: 200px;
    font-size: 20px;
    margin-bottom: 10px;
    text-align: center;
    border-radius: 4px;
  }
  
  button {
    height: 40px;
    width: 100px;
    font-size: 20px;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 5px;
  }
  .stateChange{
    transition: all 3s ease-out;
  }
  .block {
    background-color: rgb(1, 0, 0);
    color: black;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    text-align: center;
  }

  .btn:active{
    background-color: #367b39 ;
    height: 40px;
    width: 94px;
  }